.flag{
    width: 20%;
}

.flag p{
    text-align: center;
}
tbody .count{
    font-family: 'Nunito';
    font-size: 1.2rem ;

}

tbody .yesterday{
    color : #b5b5b5;
    font-weight: 400;
    font-size: 1rem;
    font-family: 'Nunito';
}

.yesterday strong {
    color: #3c3c3c;
}

.worldStats-box {
    
    height : 100%;
    width: 250px;
    float : left; 
    margin-right: 20px;
    
}

 .totalNumbers {
    font-size: 2.7rem;
    color: white;
    font-weight: 700 !important;
    font-family: 'Nunito';
    width: 100%;
    text-align: center;
    margin-top: 25%;

}

 .about {
    width: 100%;
    text-align: center;
    color: rgba(255, 255, 255, 0.9);
    margin-top: 5%;
    font-family: 'Nunito';
}

@media only screen and (max-width: 600px){

    .worldStats-box {
        height : 100%;
        width: 200px;
        float : left;
        margin-right: 50px;
    }


}
.Global {
    height : 45vh; /* vh - viewport height. 45% of the user's screen */
    width : 100%;
    background: linear-gradient(rgba(114, 5, 216, 0.9), rgba(114, 5, 216, 0.3)), url(/Covid-Tracker-App/static/media/corona_banner.52d5858a.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
}

.Global .heading{
    font-size: 3.5rem;
    font-weight: 700 !important;
    color: white;
    width: 100%;
    text-align: center;
    margin-top: 45px;


}

.Global .description {
    text-align: center;
    font-size: 1.2rem;
    color : rgba(255,255,255,0.7);
    font-family: "Nunito" , sans-serif;
    width: 100%;
    height : 20px;
    
}

.Global .world-statistics{
    width: 100%;
    height: 250px;
    display: flex;
    justify-content: center;
}
